import PropTypes from 'prop-types';
import { Badge } from 'react-bootstrap';

const SimulationCounters = ({ unaffected, infected, cured }) => (
  <div>
    <Badge pill variant="secondary">
      Незасегнати -
      {unaffected}
    </Badge>
    <Badge pill variant="secondary">
      Заразени -
      {infected}
    </Badge>
    <Badge pill variant="success">
      Излекувани -
      {cured}
    </Badge>
  </div>
);

SimulationCounters.propTypes = {
  unaffected: PropTypes.number.isRequired,
  infected: PropTypes.number.isRequired,
  cured: PropTypes.number.isRequired,
};

export default SimulationCounters;
